<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>教師專區</h3>
        </div>
        <Header />
        <div class="pageBoxOuter">
          <div class="stepTitle">
            <h4><span>Step2 - </span>選擇課程</h4>
          </div>
          <div class="pageBox">
            <div class="processBar x2"></div>
            <div class="courseFilter">
              <div class="formRow c_type">
                <label>課程類別</label>
                <select v-model="Category">
                  <option
                    v-for="c in LessonCategories"
                    :key="c.CategoryName"
                    :value="c.LessonCategoryID"
                  >
                    {{ c.CategoryName }}
                  </option>
                </select>
              </div>
              <div class="formRow c_search">
                <label>課程搜尋</label>
                <input type="text" v-model="Keyword" /><a href="" v-if="false"
                  >搜尋</a
                >
              </div>
            </div>
            <div class="courseList">
              <a
                class="courseListItem"
                :class="{ choose: l.Selected }"
                href="javascript:void(0);"
                v-for="l in Lessons"
                :key="l.LessonID"
                @click="l.Selected = !l.Selected"
              >
                <div class="courseImg">
                  <img :src="l.ImageUrl | imageCDN" />
                </div>
                <div class="courseInfo">
                  <h3>{{ l.LessonName }}</h3>
                  <div class="cateTag" v-if="l.LessonCategoryID== 1">師培課程</div>
                  <div class="cateTag" v-else>學生課程</div>
                  <div class="time">
                    <img src="/img/action.png" />{{ l.VideoLength | mmss }}
                  </div>
                </div></a
              >
            </div>
            <div class="stepControl">
              <div class="prevBtn">
                <router-link to="/teacher/class/create2">上一步</router-link>
              </div>
              <div class="nextBtn">
                <a @click="next" href="javascript:void(0);">下一步</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Teacher/Header.vue";
import ClassRoom from "@/plugins/classroom.js";

var all = [];
export default {
  data() {
    return {
      classRoom: null,
      Lessons: [],
      LessonCategories: [
        {
          LessonCatrgoryID: null,
          CategoryName: "全部",
        },
      ],
      Category: null,
      Keyword: "",
    };
  },
  components: {
    Header,
  },
  async mounted() {
    this.setBase("tSpace page");
    this.classRoom = ClassRoom.getClassRoom();
    var [categories, lessons] = await Promise.all([
      this.$api.getLessonCategories(),
      this.$api.getAllLessons(),
    ]);
    all = lessons;
    var map = {};
    this.classRoom.Lessons.forEach((l) => (map[l] = true));
    all.forEach((l) => (l.Selected = map[l.LessonID] || false));
    this.Lessons = all;
    this.LessonCategories.push(...categories);
    this.Category = this.LessonCategories[0];
  },
  methods: {
    filter() {
      var result = all;
      if (this.Category) {
        result = result.filter((x) => x.LessonCategoryID == this.Category);
      }
      if (this.Keyword) {
        result = result.filter((x) => x.LessonName.indexOf(this.Keyword) >= 0);
      }

      this.Lessons = result;
    },
    next() {
      var lessons = all.filter((x) => x.Selected).map((x) => x.LessonID);
      if (lessons.length == 0) {
        alert("請選擇課程");
        return;
      }

      this.classRoom.Lessons = lessons;
      ClassRoom.save();
      this.$router.push("/teacher/class/create/info");
    },
  },
  watch: {
    Category() {
      this.filter();
    },
    Keyword() {
      this.filter();
    },
  },
};
</script>